@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
.main_container {
  background-color: #5c48d3;
  padding-bottom: 40px;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}

.title {
  display: grid;
  color: #ffffff;
  font-size: 60px;
  font-weight: lighter;
}

.title + p {
  color: #ffffff;
  justify-content: center;
  font-size: 23px;
  font-weight: lighter;
}
@media (max-width: 620px) {
  .title + p {
    margin-left: 10px;
    color: #ffffff;
    font-size: 20px;
    font-weight: lighter;
    padding-top: 20px;
  }
}
.img {
  width: 144px;
  height: 144px;
  border-width: 12px;
  border-radius: 100px;
}

.container_fouter {
  display: flex;
  flex-direction: column;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #5c48d3;
  justify-content: center;
  align-items: center;
}

.container_fouter > p {
  flex: 1px 1;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: lighter;
  color: #ffffff;
}

.project {
  margin: 80px 15% 80px 15%;
}
@media (max-width: 720px) {
  .project {
    margin: 80px 0px 80px 0px;
  }
}
.img_project {
  height: auto;
  width: 85%;
  position: relative;
  margin: 10px 0px 10px 0px;
  border-radius: 10px;
}
@media (max-width: 720px) {
  .img_project {
    height: auto;
    width: 100%;
    position: relative;
    margin: 10px 0px 10px 0px;
    border-radius: 10px;
  }
}
.title_project {
  font-size: 23px;
  font-weight: lighter;
  color: #636363;
  padding-bottom: 60px;
}

.link_project {
  text-decoration: none;
  border: solid 1px #5c48d3;
  padding: 20px;

  border-radius: 5px;
  color: #636363;
}

.link_project:hover {
  background-color: #5c48d3;
  text-decoration: none;
  color: white;
}

.main_container {
  background-color: #5c48d3;
}
.container_sections {
  border: 1px solid transparent;
  border-radius: 15px;
  margin: 0px 10% 0px 10%;
  background-color: #ffffff;
}

@media (max-width: 620px) {
  .container_sections {
    border: 1px solid transparent;
    border-radius: 15px;
    margin: 0px 2% 0px 2%;
    background-color: #ffffff;
  }
}

.link {
  color: #636363;
  text-decoration: none;
  font-size: 18px;
  font-weight: lighter;
  padding: 10px 20px 10px 20px;
}

.item {
  margin: 5px 3% 0 3%;
}

.navBar {
  height: 60px;
  background-color: #f7f7f7;
  border: 1px solid transparent;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.liste {
  display: flex;
  flex: 1 1;
  justify-content: center;
  padding: 10px 5px 10px 5px;
  list-style: none;
  margin: 0;
  border: 0;
  font-size: 20;
  vertical-align: baseline;
}

.item :hover {
  color: #636363;
  border-radius: 10px;
  background-color: #ffffff;
  text-decoration: none;
}
.article {
  text-align: center;
  margin: 100px 50px 100px 50px;
}
.titles {
  padding-bottom: 10px;
  font-size: 32px;
  font-weight: lighter;
  margin: 150px 2% 50px 2%;
  color: #636363;
  position: relative;
}
.titles::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 10%;
  padding-top: 20px;
  border-bottom: solid 2px #5c48d3;
  margin-bottom: 20px;
}

.titles + p {
  font-weight: lighter;
  font-size: 20px;
  margin: 0 2% 0 2%;
}

@media (max-width: 620px) {
  .titles + p {
    font-size: 20px;
    font-weight: lighter;
    margin: 0;
  }
}
p {
  color: #636363;
}
.image {
  height: 180px;
  width: 90%;
  margin: 60px 10px 60px 10px;
  border-radius: 10px;
}
@media (max-width: 720px) {
  .image {
    height: 150px;
    width: 100%;
    margin: 60px 0px 60px 0px;
    border-radius: 10px;
  }
}
.competences_grid {
  text-align: justify;
  font-size: 18px;
  font-weight: lighter;
  padding: 5px;
  -webkit-column-count: 2;
          column-count: 2;
  margin: 0px 5% 0px 5%;
}

@media (max-width: 720px) {
  .competences_grid {
    -webkit-column-count: 1;
            column-count: 1;
    font-size: 16px;
    margin: 0;
  }
}

@media (max-width: 870px) {
  .navBar {
    display: none;
  }
}

.btn.btn-purpel {
  flex-shrink: 0;
  border: none;
  outline: none;
  background-color: #5c48d3;
  cursor: pointer;
  font-size: 20px;
  color: #f1f1f1;
  padding: 12px 16px;
  border-radius: 3px;
  overflow: hidden;
  position: relative;
  margin-top: 60px;
}
.btn.btn-purpel::before {
  content: "";
  position: absolute;
  background-color: #5c48d3;
  width: 0px;
  height: 0px;
  border-radius: 50%;
  left: calc(100% * var(--wavecoord-x));
  top: calc(100% * var(--wavecoord-y));
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  opacity: 0;
  -webkit-transition: opacity 0.7s, -webkit-transform 0.7s;
  transition: opacity 0.7s, -webkit-transform 0.7s;
  transition: transform 0.7s, opacity 0.7s;
  transition: transform 0.7s, opacity 0.7s, -webkit-transform 0.7s;
}
.btn.btn-purpel:active::before {
  -webkit-transition-duration: 0s;
          transition-duration: 0s;
  opacity: 1;
  -webkit-transform: translate(-50%, -50%) scale(0);
          transform: translate(-50%, -50%) scale(0);
}

.btn.btn-purpel:hover {
  color: #5c48d3;
  background: white;
  border: 1px solid #5c48d3;
}

.form {
  margin: 80px 25% 15px 25%;
}
@media (max-width: 620px) {
  .form {
    margin: 80px 5% 15px 5%;
  }
}
.message_succes {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
}

body {
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}
button:focus {
  outline: 0;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}

